 /*============= google fonts ==========*/

  @import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=PT+Sans:ital@1&display=swap');

   /*============= Variables Css ==========*/
   :root{
     /*=============Colors ==========*/
     --first-color: hsl(353,100%, 65%);
     --title-color: hsl(244,24%,26%);
     --text-color: hsl(244,16%,43%);
     --body-color: hsl(258,60%,98%);
     --container-color: #fff;
     --list-bg-color:  hsl(244,16%,43%);

      /*=============Font And Typography ==========*/
      --body-font-family: 'Catamaran',' sans-serif';
      --h1-font-size:2.25rem;
      --h2-font-size:1.5rem;
      --h3-font-size:1.25rem;
      --normal-font-size:2rem;
      
      --smal-font-size: 0.875rem;
      --smaller-font-size: 0.813rem;
     /*=============Font weight ==========*/
     --font-medium: 500;
     --font-semibold: 600;
     --font-bold: 700;

     /*=============BOX Shadow==========*/
     --shadow: 0px 5px 20px rgba(69 67 96 / 10%);

      /*=============Border-Radius ==========*/
     --border-radius: 20px;

   }



 /*==================================== responsive typography ========================= ==========*/
@media screen and (max-width:1024px) {
  :root{
    --h1-font-size:1.75rem;
      --h2-font-size:1.25rem;
      --h3-font-size:1rem;
      --normal-font-size:0.938rem;
      --smal-font-size: 0.813rem;
      --smaller-font-size: 0.75rem;
  }

}

 /*==================================== BASE ========================= ==========*/

*{
  margin: 0;
  padding: 0;
  box-sizing:  border-box;
  /* overflow-y: hidden; */
}

html{
  scroll-behavior: border-box;
}

body,
button,
input,
textarea{
  font-family: var(--body-font-family);
  font-size: var(--normal-font-size);

}

body{
  background-color: var(--body-color);
  line-height: 1.7;
  color: var(--text-color);
}

h1, h2,h3{
  color: var(--text-color);
  font-weight: var(--font-semibold);
  line-height: 1.2;
}
ul{
  list-style: none;
}

a{
  text-decoration: none;
}
button{
  cursor: pointer;
  border: none;
  outline: none;
}
img{
  max-width: 100%;
  height:auto;
  vertical-align: middle;
}

.container{
  max-width: 1000px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}
.grid{
  display: grid;
}
.section{
  padding-top: 7rem;
  padding-bottom: 2rem;
}
.section-title{
  font-size: var(--h1-font-size);
  margin-left: 0.875rem;
  font-weight: var(--font-bold);
  margin-bottom: 3.75rem;
}

.section-title::before{
  content: '';
  /* background:url(); */
  height: 2.25rem;
  width: 2.25rem;
  position: absolute;
  left: -0.857rem;
  top: -0.875;
}
.btn{
  cursor: pointer;
  padding: 0.75rem 2rem;
  line-height: 1;
  border-radius: 1.875rem;
  box-shadow: 0 0 1px rgb(0, 0, 0 / 0%);
  color: #fff;
  display: inline-block;
  background-color: var(--first-color);
  font-weight: var(--font-bold);
 
}
.btn:hover{
   animation: button-push 0.3s ease 0.5;
}

 /*==================================== BASE ========================= ==========*/

 @keyframes button-push {
  50%{
      transform: scale(0.8);
  
    }
    100%{
      transform: scale(11);
    }
   };


    /*==================================== Responsiv ========================= ==========*/
 

