.contact.section{
    padding-bottom: 6.25rem;
}
.contact_container{
    grid-template-columns: 4fr 8fr;
    column-gap: 1.875rem;
}
.contact_title{
    font-size: var(--h3-font-size);
    margin-bottom: .5rem;

}
.contact_form-group{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    column-gap: 1.5rem;
}
.contact_form-div{
    position: relative;
    margin-bottom: 1.875rem;
    height: 3.75rem;

}
.contact_form-input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: var(--shadow);
    background-color: var(--container-color);
    color: var(--text-color);
    border: none;
    outline: none;
    border-radius: 1.875rem;
    padding: 0.625rem 1.875rem;
    z-index: 1.;
}

.contact_form-erea{
    height: 10.25rem;
}

.contact_form-erea textarea{
    resize: none;
}

.number{
    padding-left: 5px;
    display: none;
}
.phone{
    margin: 10px 10px;
}
.send{
    margin-right: 10px;
}
.phone:hover{
    .number{
        display: inline;
    }

}


@media screen and (max-width:1000px){


.contact_container{
    grid-template-columns: repeat(1,1fr);
}


    .number{
      display: inline;
      animation: num 0.3s ease 0.5;    }
    


}


@keyframes num {
    50%{
       opacity: 0;
    
      }
      100%{
        opacity: 1;
      }
     };
  
